<script>
export default {
  data () {
    return {
      product: {
        sortName: '',
        productName: '',
        productPrice: '',
        productDescription: '',
        productStock: '',
        productsrc: '',
        productid: ''
      },
      count: ''
    }
  },
  computed: {
    id () {
